import * as apiTypes from '../../types/data-item-types'
import { PipelineStage } from './stages'

/**
 * @builder
 */
export interface ObjectToArrayStage extends PipelineStage {}

export class ObjectToArrayStageImpl implements ObjectToArrayStage {
  constructor(
    private objectFieldName: string,
    private destinationFieldName: string
  ) {}

  toProto(): apiTypes.Stage {
    return {
      objectToArray: {
        objectFieldName: this.objectFieldName,
        destinationFieldName: this.destinationFieldName,
      },
    }
  }
}
